.auth {
    min-height: 466px;
    width: 407px;
    border-radius: 6px;
    padding: 26px 31px 52px;
    box-shadow: 1px 8px 25px rgba(168, 168, 168, 0.13);

    &_title {
        font-weight: bold;
        font-size: 24px;
        line-height: 29px;
        color: #7367F0;
        text-align: center;
        margin-bottom: 32px;
    }

    &_subtitle {
        font-weight: 500;
        margin-bottom: 8px;
    }

    &_description {
        font-size: 14px;
        line-height: 21px;
        margin-bottom: 20px;
    }

    &_field {
        margin-bottom: 16px;
    }

    &_form {
        margin-bottom: 20px;

        &_errors {
            list-style: none;
            color: red;
            margin-bottom: 20px;
        }
    }

    &_bottom {
        display: flex;
        justify-content: space-between;
    }
}